.button{
    background-color: rgba(23, 26, 32, 0.8);
    height: 38px;
    width: 256px;
    margin: 8px;
    display: flex;
    justify-items: center;
    align-items: center;
    border-radius: 5px;
    opacity: 0.85;
}

.button a{
text-decoration: none;
color: white;
font-size: 12px;
width: 100%;
text-align: center;
}
.button__white{
    background-color: white;
    opacity: 0.65;
    width: 260px;
}
.button__white>a{
    color: black;
}
